<template>
  <div :style="{ backgroundColor: '#F2F2F2'}">
    <SimpleHeader name="我的设备"/>
    <div class="scllow_my_device">
      <div>
        <div v-for="(item, index) in devices" :key="index" >
          <div class="item_my_device">{{ item.desc }}</div>
          <van-swipe-cell :before-close="beforeClose">

            <van-cell center :title="item.name">
              <template #right-icon>
                <van-switch v-model="item.checked"  active-color="#07B38E" size="24" />
              </template>
            </van-cell>

            <template #right>
              <van-button square type="danger" text="删除" />
            </template>
          </van-swipe-cell>

        </div>
      </div>

    </div>
  </div>


</template>

<script>
import SimpleHeader from "@/components/SimpleHeader";
import { reactive, toRefs } from "vue";
import { Dialog } from 'vant';
export default {
name: "index",
  components: {SimpleHeader},
  setup(props){
    const state = reactive({
      devices: [
        {
          id: 0,
          name: 'DATA_SPORT',
          desc: '运动',
          checked: true,
        },
        {
          id: 1,
          name: 'DATA_SLEEP',
          desc: '睡眠',
          checked: false,
        },
        {
          id: 2,
          name: 'DATA_BLOOD_PRESSURE',
          desc: '血压',
          checked: false,
        },
        {
          id: 3,
          name: 'DATA_BLOOD_GLUCOSE',
          desc: '血糖',
          checked: false,
        },
        {
          id: 6,
          name: 'DATA_SLIMMING',
          desc: '瘦身',
          checked: false,
        },
        {
          id: 7,
          name: 'DATA_HEART',
          desc: '心率',
          checked: false,
        },
        {
          id: 10,
          name: 'DATA_SLEEP_PRO',
          desc: '专业睡眠数据',
          checked: false,
        },
      ],
    })

    // position 为关闭时点击的位置
    const beforeClose = ({ position }) => {
      switch (position) {
        case 'left':
        case 'cell':
        case 'outside':
          return true;
        case 'right':
          return new Promise((resolve) => {
            Dialog.confirm({
              title: '确认解绑该设备',
            }).then(resolve);
          });
      }
    };

    return{
      ...toRefs(state),
      beforeClose
    }
  }
}
</script>

